.body {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.selector{
    max-width: 200px;
}
.bodyItems{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.item{
    position: relative;
    border: none;
    margin: 5px;
    height: 150px;
    width: 150px;
}

.favoriteImage{
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.favoriteButtonContainer{
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    position: absolute;
}
.favoriteButton {
    width: 100%;
    height: 100%;
    border: none;
    background: rgba(0, 0, 0, 0);
}
@media (any-hover: hover) {
    .favoriteButton:hover {
        background: rgba(0, 0, 0, 0.4);
    }
}